<template>
    <div class="star" :class="starType">
        <span
            v-for="(itemClass,index) in itemClasss"
            :key="index"
            :class="itemClass"
            class="star-item"
        ></span>
    </div>
</template>  


<script>
const NUM = 5,
    CLA_ON = "on",
    CLA_OFF = "off",
    CLA_HALF = "half";
export default {
    props: {
        size: {
            type: String,
            default: "48"
        },
        score: {
            type: [String,Number],
            default: "5"
        }
    },
    computed: {
        starType() {
            return `star-${this.size}`;
        },
        itemClasss() {
            const itemClasss = [];
            const score = Math.floor(this.score * 2) / 2;
            const hasHalf = score % 1 !== 0;
            const integer = Math.floor(score);
            for (let i = 0; i < integer; i++) {
                itemClasss.push("on");
            }
            hasHalf && itemClasss.push("half");
            while (NUM > itemClasss.length) {
                itemClasss.push("off");
            }
            return itemClasss;
        }
    }
};
</script>


<style lang="stylus">
.star
    display flex
    justify-content center
    align-items center
    .star-item
        background-repeat no-repeat
    &.star-48
        .star-item
            width 20px
            height 20px
            margin-right 22px
            background-size 20px 20px
            &.on
                background-image url('star48_on@2x.png')
            &.off
                background-image url('star48_off@2x.png')
            &.half
                background-image url('star48_half@2x.png')
    &.star-36
        .star-item
            width 15px
            height 15px
            margin-right 22px
            background-size 15px 15px
            &.on
                background-image url('star36_on@2x.png')
            &.off
                background-image url('star36_off@2x.png')
            &.half
                background-image url('star36_half@2x.png')
    &.star-24
        .star-item
            width 12px
            height 12px
            margin-right 22px
            background-size 12px 12px
            &.on
                background-image url('star24_on@2x.png')
            &.off
                background-image url('star24_off@2x.png')
            &.half
                background-image url('star24_half@2x.png')
</style>